.contact_content {
    height: 90%;
    top: 5%;
    left: 55px;
    min-height: 566px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    position: absolute;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: calc(100% - 55px);
}

.contact_content .text_h1 {
    color: #08fdd8;
}

.contact_content > .contact_main {
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.contact_main > .left_side {
    margin-left: 6%;
    width: 35%;
    vertical-align: middle;
    display: table-cell;
    max-height: 90%;
}

.contact_main form {
    width: 100%;
    opacity: 0.7;
}

.contact_main form div.input_row:not(:first-child) {
    padding: 0;
    margin: 0 0 10px;
    /*opacity: 0;*/
    overflow: hidden;
    display: block;
    clear: both;
    position: relative;
}

.contact_main form div.input_row:first-child{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 0 0 10px;
    /*opacity: 0;*/
    overflow: hidden;
    clear: both;
    position: relative;

}

.contact_main form > div.input_row input {
    width: 100%;
    border: 0;
    background: #37393b;
    height: 50px;
    font-size: 16px;
    color: #8d8d8d;
    padding: 0 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.contact_main form div.input_row div.half > input {
    width: 99%;
    border: 0;
    background: #37393b;
    height: 50px;
    font-size: 16px;
    color: #8d8d8d;
    padding: 0 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.contact_main form div.input_row div.half:nth-child(2) > input {
    margin-left: 2%;
}

.contact_main form > div > textarea {
    width: 100%;
    border: 0;
    background: #37393b;
    height: 50px;
    font-size: 16px;
    color: #8d8d8d;
    padding: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    min-height: 150px;
    max-height: 250px;
    resize: vertical;
}

.contact_main form > div > input#submit {
    color: #08fdd8;
    font-size: 11px;
    letter-spacing: 3px;
    font-family: "Open Sans", sans-serif;
    text-decoration: none;
    padding: 8px 10px;
    border: 1px solid #08fdd8;
    border-radius: 4px;
    background: transparent;
    text-transform: uppercase;
    float: right;
    text-align: center;
    margin-right: 10px;
}

.contact_main > .right_side {
    width: 65%;
    height: 114%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    position: absolute;
    right: -5%;
    top: -6%;
    -webkit-clip-path: polygon(0% 0%,100% 0%,100% 100%,25% 100%);
            clip-path: polygon(0% 0%,100% 0%,100% 100%,25% 100%);
}

.contact_main > .right_side > div#map {
    height: 400px;
    width: 100%;
}


