/* |||         MAIN DIV         |||*/
.home_content {
    height: 100%;
    width: calc(100% - 55px);
    margin-left: 55px;

}

/* |||         LEFT SIDE         |||*/
.home_content .left_side {
    height: 90%;
    top: 5%;
    min-height: 566px;
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 100%;
    z-index: 1;
}

.home_content .home_main {
    margin-left: 6%;
}

.home_content .h2_greating {
    color: #8d8d8d;
    margin-top: 9px;
    font-weight: normal;
    font-size: 11px;
    font-family: "Open Sans", sans-serif;
    letter-spacing: 3px;
    -webkit-animation: myanim2 1s 1.8s backwards;
    animation: myanim2 1s 1.8s backwards;

}

.home_content .contact_btn {
    color: #08fdd8;
    font-size: 13px;
    letter-spacing: 3px;
    font-family: "Open Sans", sans-serif;
    text-decoration: none;
    padding: 8px 12px;
    border: 1px solid #08fdd8;
    margin-top: 25px;
    float: left;
    border-radius: 3px;
    -webkit-transition: 0.7s;
    -o-transition: 0.7s;
    transition: 0.7s;
    text-transform: uppercase;
    z-index: 2;
}

.home_content .contact_btn:hover {
    background: #08fdd8;
    color: #252627;
}

/* |||         RIGHT SIDE         |||*/
.home_content .right_side {
    position: absolute;
    right: -123px;
}

.home_content svg.logo {
    width: 759px;
    height: 286px;
    margin-top: 155px;
    font-family: 'MyLogo', cursive;

    text-shadow: -1px 0 28px #08fdd842;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

.home_content svg.logo > .path, svg.logo_mirror > .path {
    stroke-dasharray: 1200;
    stroke-dashoffset: 0;
    -webkit-animation: dash 5s linear alternate both, neonBlink 5s infinite;
    animation: dash 5s linear alternate both, neonBlink 5s infinite;
}

.home_content svg.logo_mirror {
    width: 713px;
    height: 224px;
    position: absolute;
    top: 314px;
    right: 156px;

    font-family: 'MyLogo', cursive;
    -webkit-transform: rotate(-135deg) rotateY(180deg);
            transform: rotate(-135deg) rotateY(180deg);
    -webkit-filter: blur(2px);
    -webkit-box-shadow: inset 0 -40px 50px -10px #0000000a;
            box-shadow: inset 0 -40px 50px -10px #0000000a;
}

@keyframes dash {
    0% {
        stroke-dashoffset: 1200;
        fill-opacity: 0;
    }
    80% {
        fill-opacity: 0;
    }
    100% {
        stroke-dashoffset: 0;
        fill-opacity: 1;
    }
}

@-webkit-keyframes dash {
    0% {
        stroke-dashoffset: 1200;
        fill-opacity: 0
    }
    80% {
        fill-opacity: 0
    }
    100% {
        stroke-dashoffset: 0;
        fill-opacity: 1
    }
}

@keyframes neonBlink {
    0% {
        opacity: 1;
    }
    10% {
        opacity: .6;
    }
    12% {
        opacity: 1;
    }
    15% {
        opacity: .4;
    }
    17% {
        opacity: 1;
    }
    18% {
        opacity: .3;
    }
    19% {
        opacity: 1;
    }
    29% {
        opacity: 1;
    }
    30% {
        opacity: .9;
    }
    33% {
        opacity: 1;
    }
    89% {
        opacity: 1;
    }
    91% {
        opacity: .7;
    }
    94% {
        opacity: 1;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes neonBlink {
    0% {
        opacity: 1;
    }
    10% {
        opacity: .6;
    }
    12% {
        opacity: 1;
    }
    15% {
        opacity: .4;
    }
    17% {
        opacity: 1;
    }
    18% {
        opacity: .3;
    }
    19% {
        opacity: 1;
    }
    29% {
        opacity: 1;
    }
    30% {
        opacity: .9;
    }
    33% {
        opacity: 1;
    }
    89% {
        opacity: 1;
    }
    91% {
        opacity: .7;
    }
    94% {
        opacity: 1;
    }
    100% {
        opacity: 1;
    }
}




