header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-direction: column;
        flex-direction: column;
    position: absolute;
    top: 0;
    width: 55px;
    height: 100%;
    min-height: 400px;
    z-index: 3;
    background: #181818;
    /*cursor: pointer;*/
}

header > div.logo {
    background: #070707;
    display: block;
    padding: 5px 0 0;
}

header > div.logo > img {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    display: block;
    margin: 0 auto;
    height: auto;
}

header > div.logo > span {
    display: block;
    margin: 0 auto 5px;
    font-size: 15px;
    height: auto;
    color: white;
    text-align: center;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

header > div.main_buttons {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-line-pack: center;
        align-content: center;
    text-align: center;
    width: 100%;
    height: 300px;
}

.main_buttons a {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    /*cursor: pointer;*/
    color: #4d4d4e;
    height: 100%;
    -ms-flex-pack: center;
        justify-content: center;
    background: #181818;
    width: 55px;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.main_buttons a:hover {
    width: 65px;
}

.fa-user, .fa-home, .fa-cog, .fa-eye, .fa-envelope {
    text-transform: uppercase;
    font-size: 22px;
    display: block;
    line-height: 51px;
    margin: 0 auto;
    height: 20px;
    position: relative;
    text-decoration: none;
    /*cursor: pointer;*/
}

.main_buttons a span {
    font-weight: lighter;
    text-transform: uppercase;
    font-size: 8px;
    padding: 0;
    margin: 3px auto 0;
    opacity: 0;
}

.main_buttons a:hover, .main_buttons a:hover span {
    opacity: 1;
    color: #08fdd8;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    text-decoration: none;
}

.fa-user:hover, .fa-home:hover, .fa-cog:hover, .fa-eye:hover, .fa-envelope:hover, .fa-telegram-plane:hover, .fa-facebook-f:hover, .fa-instagram:hover {
    color: #08fdd8;
}

header > div.social_buttons {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-line-pack: center;
        align-content: center;
    text-align: center;
    width: 100%;
}

.fa-telegram-plane, .fa-facebook-f, .fa-instagram {
    display: block;
    padding: 5px 0;
    margin: 0 auto;
    height: 30px;
    font-size: 15px;
    line-height: 16px;
    color: #4d4d4e;
    /*cursor: pointer;*/
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}



